<template>
  <div class="countBox">
    <button @click="countSub()" class="min">-</button>
    <input :value="value" @change="handleChange" type="text">
    <button @click="countAdd()" class="max">+</button>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: Number,
      default: 1
    }
  },
  methods: {
    countSub () {
      if (this.value <= 1) {
        return
      }
      this.$emit('input', this.value - 1)
    },
    countAdd () {
      this.$emit('input', this.value + 1)
    },
    handleChange (e) {
      const a = +e.target.value
      if (isNaN(a) || a < 1) {
        e.target.value = this.value
        return
      }
      this.$emit('input', a)
    }
  }

}
</script>

<style lang="less" scoped>
  .countBox{
    padding: 0 5px;
    width: 130px;
    // border: 0;
    // background-color: aquamarine;

    .min,.max{
      float: left;
      width: 30px;
      height: 30px;
      border: 0;
      background-color: #efefef;
    }
    input{
      float: left;
      width: 40px;
      height: 30px;
      outline: none;
      border: 0;
      // text-indent: 4px;
      font-size: 13px;
      text-align: center;
      background-color: #efefef;
      margin: 0 10px;
    }
  }
</style>
