import { deleteCart, getMyCart, updateCart } from '@/api/shopping'
import { Toast } from 'vant'

export default {
  namespaced: true,
  state () {
    return {
      cartList: []
    }
  },
  mutations: {
    giveCartList (state, list) {
      state.cartList = list
    },
    foundCheck (state, id) {
      const res = state.cartList.find(item => item.goods_id === id)
      res.isChecked = !res.isChecked
    },
    allChecked (state, flag) {
      state.cartList.forEach(item => { item.isChecked = flag })
    },
    changeCount (state, obj) {
      const res = state.cartList.find(item => item.goods_id === obj.goodsId)
      res.goods_num = obj.goodsNum
    }

  },
  getters: {
    getCartCount (state) {
      return state.cartList.reduce((sum, item) => sum + item.goods_num, 0)
    },
    getNeed (state) {
      return state.cartList.filter(item => item.isChecked === true)
    },
    getNeedCount (state, getters) {
      return getters.getNeed.reduce((sum, item) => sum + item.goods_num, 0)
    },
    getRePrice (state, getters) {
      return getters.getNeed.reduce((sum, item) => sum + item.goods_num * item.goods.goods_price_min, 0).toFixed(2)
    },
    isAllChecked (state) {
      return state.cartList.every(item => item.isChecked)
    }

  },
  actions: {
    async getCartList (context) {
      const { data } = await getMyCart()
      data.list.forEach(item => {
        item.isChecked = true
      })
      console.log(data)
      context.commit('giveCartList', data.list)
    },
    async upCart (context, obj) {
      const { goodsNum, goodsId, goodsSkuId } = obj

      context.commit('changeCount', { goodsNum, goodsId })

      await updateCart(goodsId, goodsNum, goodsSkuId)
      // console.log(res)
    },
    subCart (context) {
      const ids = context.getters.getNeed.map(item => item.id)
      // console.log(res)
      Toast('删除成功')
      setTimeout(async () => {
        await deleteCart(ids)

        context.dispatch('getCartList')
      }, 1000)
    }
  }
}
