<template>
  <div class="prodetail">
    <van-nav-bar
      fixed
      title="商品详情页"
      left-arrow
      @click-left="$router.go(-1)"
    />

    <van-swipe :autoplay="3000" @change="onChange">
      <van-swipe-item v-for="(image, index) in images" :key="index">
        <img :src="image.external_url" />
      </van-swipe-item>

      <template #indicator>
        <div class="custom-indicator">
          {{ current + 1 }} / {{ images.length }}
        </div>
      </template>
    </van-swipe>

    <!-- 商品说明 -->
    <div class="info">
      <div class="title">
        <div class="price">
          <span class="now">{{ list.goods_price_min }}</span>
          <span class="oldprice">{{ list.line_price_min }}</span>
        </div>
        <div class="sellcount">已售{{ list.goods_sales }}件</div>
      </div>
      <div class="msg text-ellipsis-2">
        {{ list.goods_name }}
      </div>

      <div class="service">
        <div class="left-words">
          <span><van-icon name="passed" />七天无理由退货</span>
          <span><van-icon name="passed" />48小时发货</span>
        </div>
        <div class="right-icon">
          <van-icon name="arrow" />
        </div>
      </div>
    </div>

    <!-- 商品评价 -->
    <div class="comment">
      <div class="comment-title">
        <div class="left">商品评价 ({{ remarkLen }}条)</div>
        <div class="right">查看更多 <van-icon name="arrow" /></div>
      </div>
      <div class="comment-list">
        <div class="comment-item" v-for="item in remark" :key="item.comment_id">
          <div class="top">
            <img :src="item.user.avatar_url || defaultImg" alt="" />
            <div class="name">{{ item.user.nick_name }}</div>
            <van-rate
              :size="16"
              :value="item.score / 2"
              color="#ffd21e"
              void-icon="star"
              void-color="#eee"
            />
          </div>
          <div class="content">{{ item.content }}</div>
          <div class="time">{{ item.create_time }}</div>
        </div>
      </div>
    </div>

    <!-- 商品描述 -->
    <div class="desc" v-html="list.content"></div>

    <!-- 底部 -->
    <div class="footer">
      <div @click="$router.push('/')" class="icon-home">
        <van-icon name="wap-home-o" />
        <span>首页</span>
      </div>
      <div @click="$router.push('/cart')" class="icon-cart">
        <van-icon name="shopping-cart-o" />
        <van-badge class="badge" :content="cartTotal" />
        <span>购物车</span>
      </div>
      <div @click="addMyShopping" class="btn-add">加入购物车</div>
      <div @click="nowBuy" class="btn-buy">立刻购买</div>
    </div>

    <van-action-sheet
      v-model="show"
      :title="veto === true ? '加入购物车' : '立刻购买'"
    >
      <div class="product">
        <div class="product-title">
          <div class="left">
            <img :src="list.goods_image" alt="" />
          </div>
          <div class="right">
            <div class="price">
              <span>¥</span>
              <span class="nowprice">{{ list.goods_price_min }}</span>
            </div>
            <div class="count">
              <span>库存</span>
              <span>{{ list.stock_total }}</span>
            </div>
          </div>
        </div>
        <div class="num-box">
          <span>数量</span>
          <CountBox v-model="getCount"></CountBox>
        </div>
        <div class="showbtn" v-if="list.stock_total > 0">
          <div @click="isToken" class="btn" v-if="veto">加入购物车</div>
          <div @click="goBuyNow" class="btn now" v-else>立刻购买</div>
        </div>
        <div class="btn-none" v-else>该商品已抢完</div>
      </div>
    </van-action-sheet>
  </div>
</template>

<script>
import { getDetailList, getRemark, addCart } from '@/api/prodetail'
import defaultImg from '@/assets/default-avatar.png'
import CountBox from '@/components/CountBox.vue'
// import { mapGetters } from 'vuex'
import loginConfirm from '@/mixins/loginConfirm'

export default {
  name: 'ProDetail',
  mixins: [loginConfirm],
  components: {
    CountBox
  },
  data () {
    return {
      images: [],
      current: 0,
      list: [],
      remark: [],
      remarkLen: 0,
      defaultImg,
      show: false,
      veto: true,
      getCount: 1,
      cartTotal: 0
    }
  },
  computed: {
    getId () {
      return this.$route.params.id
    }
    // ...mapGetters('cart', ['getCartCount'])
  },
  methods: {
    onChange (index) {
      this.current = index
    },
    async getDetList () {
      const id = this.getId
      const res = await getDetailList(id)
      const dt = res.data.detail
      this.images = dt.goods_images
      this.list = dt
      console.log(dt)
    },
    async getReList () {
      const id = this.getId
      const {
        data: { list, total }
      } = await getRemark(id, 3)

      console.log(list)
      this.remark = list
      this.remarkLen = total
    },
    addMyShopping () {
      this.veto = true
      this.show = true
    },
    nowBuy () {
      this.veto = false
      this.show = true
    },
    async isToken () {
      if (this.isConfirm()) {
        return
      }

      const { data: { cartTotal } } = await addCart(this.getId, this.getCount, this.list.skuList[0].goods_sku_id)
      // console.log(res)
      this.cartTotal = cartTotal
      this.$toast('加入成功')
      this.show = false
    },
    goBuyNow () {
      if (this.isConfirm()) {
        return
      }

      this.$router.push({
        path: '/pay',
        query: {
          mode: 'buyNow',
          goodsId: this.getId,
          goodsSkuId: this.list.skuList[0].goods_sku_id,
          goodsNum: this.getCount
        }
      })
      // console.log(res)
    }
  },
  created () {
    this.getDetList()
    this.getReList()
    // console.log(this.getCartCount)
    // this.cartTotal = this.getCartCount
  }
}
</script>

<style lang="less" scoped>
.prodetail {
  padding-top: 46px;
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  img {
    display: block;
    width: 100%;
  }
  .custom-indicator {
    position: absolute;
    right: 10px;
    bottom: 10px;
    padding: 5px 10px;
    font-size: 12px;
    background: rgba(0, 0, 0, 0.1);
    border-radius: 15px;
  }
  .desc {
    width: 100%;
    overflow: scroll;
    ::v-deep img {
      display: block;
      width: 100% !important;
    }
  }
  .info {
    padding: 10px;
  }
  .title {
    display: flex;
    justify-content: space-between;
    .now {
      color: #fa2209;
      font-size: 20px;
    }
    .oldprice {
      color: #959595;
      font-size: 16px;
      text-decoration: line-through;
      margin-left: 5px;
    }
    .sellcount {
      color: #959595;
      font-size: 16px;
      position: relative;
      top: 4px;
    }
  }
  .msg {
    font-size: 16px;
    line-height: 24px;
    margin-top: 5px;
  }
  .service {
    display: flex;
    justify-content: space-between;
    line-height: 40px;
    margin-top: 10px;
    font-size: 16px;
    background-color: #fafafa;
    .left-words {
      span {
        margin-right: 10px;
      }
      .van-icon {
        margin-right: 4px;
        color: #fa2209;
      }
    }
  }

  .comment {
    padding: 10px;
  }
  .comment-title {
    display: flex;
    justify-content: space-between;
    .right {
      color: #959595;
    }
  }

  .comment-item {
    font-size: 16px;
    line-height: 30px;
    .top {
      height: 30px;
      display: flex;
      align-items: center;
      margin-top: 20px;
      img {
        width: 20px;
        height: 20px;
      }
      .name {
        margin: 0 10px;
      }
    }
    .time {
      color: #999;
    }
  }

  .footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 55px;
    background-color: #fff;
    border-top: 1px solid #ccc;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    .icon-home,
    .icon-cart {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      position: relative;
      font-size: 14px;
      .van-icon {
        font-size: 24px;
      }
      .badge{
        position: absolute;
        top: -4px;
        right: -2px;
      }
    }
    .btn-add,
    .btn-buy {
      height: 36px;
      line-height: 36px;
      width: 120px;
      border-radius: 18px;
      background-color: #ffa900;
      text-align: center;
      color: #fff;
      font-size: 14px;
    }
    .btn-buy {
      background-color: #fe5630;
    }
  }
}

.tips {
  padding: 10px;
}

.product {
  .product-title {
    display: flex;
    .left {
      img {
        width: 90px;
        height: 90px;
      }
      margin: 10px;
    }
    .right {
      flex: 1;
      padding: 10px;
      .price {
        font-size: 14px;
        color: #fe560a;
        .nowprice {
          font-size: 24px;
          margin: 0 5px;
        }
      }
    }
  }

  .num-box {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    align-items: center;
  }

  .btn,
  .btn-none {
    height: 40px;
    line-height: 40px;
    margin: 20px;
    border-radius: 20px;
    text-align: center;
    color: rgb(255, 255, 255);
    background-color: rgb(255, 148, 2);
  }
  .btn.now {
    background-color: #fe5630;
  }
  .btn-none {
    background-color: #cccccc;
  }
}
</style>
