<template>
  <div class="login">
    <van-nav-bar
      title="标题"
      left-text
      @click-left="$router.go(-1)"
      left-arrow
    />
    <div class="login_bod">
      <div class="tit">手机号登录</div>
      <p class="alert">未注册用户手机号登陆后将自动注册</p>
      <div class="fir_tel inp_ind">
        <input v-model="mobile" type="text" placeholder="请输入手机号码" />
      </div>
      <div class="shape inp_ind">
        <input v-model="picCode" type="text" name="" id="" placeholder="请输入图形验证码" />
        <img @click="getShape" v-if="picUrl" :src="picUrl" alt="" />
      </div>
      <div class="tel_resolve inp_ind">
        <input v-model="smsCode" type="text" name="" id="" placeholder="请输入短信验证码" />
        <span @click="getCodeSecond">{{totalSecond === second ? '获取验证码' : second +'秒后重新发送'}}</span>
      </div>
      <button @click="loginEnd">登录</button>
    </div>
  </div>
</template>

<script>
// import request from '@/utils/request'
import { getPicCode, getMsgCode, codeLogin } from '@/api/login'

export default {
  name: 'LoginIndex',
  data () {
    return {
      smsCode: '246810',
      mobile: '13177733680',
      picCode: '1234', // 用户输入的图形验证码
      picKey: '', // 将来用于传递的图形验证码的唯一标识
      picUrl: '', // 储存请求渲染的图片地址
      totalSecond: 60, // 倒计时总秒数
      second: 60, // 倒计时秒数
      timer: null// 定时器
    }
  },
  created () {
    this.getShape()
  },
  methods: {
    async getShape () {
      const { data: { base64, key } } = await getPicCode()
      this.picKey = key
      this.picUrl = base64
      // this.$toast('aaaa')
    },
    // updateCode () {
    //   this.getShape()
    //   // console.log('aaa')
    // }
    validFn () {
      if (!/^1[3-9]\d{9}$/.test(this.mobile)) {
        this.$toast('请输入正确的号码')
        return false
      }
      if (!/^\w{4}$/.test(this.picCode)) {
        this.$toast('请输入正确的图形验证码')
        return false
      }
      return true
    },
    async getCodeSecond () {
      if (!this.validFn()) {
        return
      }

      if (!this.timer && this.second === this.totalSecond) {
        const res = await getMsgCode(this.picCode, this.picKey, this.mobile)
        console.log(res)
        this.$toast('短信发送成功，请注意查收')

        this.timer = setInterval(() => {
          this.second--

          if (this.second <= 0) {
            clearInterval(this.timer)
            this.timer = null
            this.second = this.totalSecond
          }
        }, 1000)
        // const res = await getMsgCode(this.picCode, this.picKey, this.mobile)
        // console.log(res)
        // this.$toast('短信发送成功，请注意查收')
      }
    },
    async loginEnd () {
      if (!this.validFn()) {
        return
      }

      if (!/^\d{6}$/.test(this.smsCode)) {
        this.$toast('请输入正确的手机验证码')
        return
      }

      const res = await codeLogin(this.mobile, this.smsCode)
      console.log(res)
      this.$store.commit('user/setUserInfo', res.data)
      this.$toast('登录成功')

      const url = this.$route.query.backUrl || '/'
      this.$router.replace(url)
    }
  },
  destroyed () {
    clearInterval(this.timer)
  }
}
</script>

<style lang="less" scoped>
.login_bod {
  margin: 45px 30px;

  .tit {
    font-weight: 400;
    font-size: 22px;
    margin-bottom: 10px;
  }
  .alert {
    color: #ccc;
    font-size: 13px;
  }
  .inp_ind {
    width: 100%;
    height: 40px;
    // background-color: aqua;
    border-bottom: 1px solid #ccc;
    // padding-right: 30px;
    margin: 20px 0;
  }
  .shape {
    img {
      width: 30%;
      float: right;
    }
  }
  .tel_resolve {
    span {
      float: right;
      line-height: 40px;
      font-size: 13px;
      color: rgb(235, 191, 110);
    }
  }
  button {
    width: 100%;
    height: 40px;
    margin-top: 20px;
    border: 0;
    border-radius: 50px;
    font-size: 13px;
    color: white;
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.1);
    background-image: linear-gradient(
      to right,
      rgb(249, 200, 107),
      rgb(255, 153, 0)
    );
  }
}
input {
  outline: none;
  border: 0;
  font-size: 13px;
  height: 100%;
  width: 60%;
  // border-right:1px solid black ;
}
</style>
