export default {
  data () {

  },
  methods: {
    isConfirm () {
      if (!this.$store.getters.token) {
        this.$dialog.confirm({
          cancelButtonText: '再逛逛',
          confirmButtonText: '去登录',
          title: '温馨提示',
          message: '此时需要先登录才能操作哦'

        })
          .then(() => {
          // on confirm
            this.$router.replace({
              path: '/login',
              query: {
                backUrl: this.$route.fullPath
              }
            })
          })
          .catch(() => {
          // on cancel
          })
        return true
      }
      return false
      // console.log('have token')
    }
  }
}
